import React, { Component } from 'react';
import './index.css';

class LoginSignup extends Component {
	constructor() {
		super();
    this.state = {
      data: []
    }
    	
    }

	render(){
		return(
			<div className='login-signup'>
				<div className='login'>
					<h1>Login</h1>
					<p>Get back to business!</p>
          <form method='POST' action='/api/signup' encType='application/x-www-form-urlencoded' onSubmit={event => {
            console.log(event.target.username.value)
    event.preventDefault()
   
  } }>
    <input name="username" onChange={this.handleUsernameChange}/>
    <input name="password" type="password" onChange={this.handlePasswordChange} />
    <input type="submit" value="Signup" />
  </form>
        
				</div>
				
				</div>
			
		)
  	}
   
handleUsernameChange = (event) => {
    this.setState({ username: event.target.value });
  }
  handlePasswordChange = (event) => {
    this.setState({ password: event.target.value });
  }
 
  
}

export default LoginSignup;



